import React, { useEffect, useState } from 'react';
import { Navbar, Nav, Container } from 'react-bootstrap';
import { NavLink,Link } from 'react-router-dom';
import logo from "../assets/images/logo.png";
import menuIcon from "../assets/images/menu.png"
const MyNavbar = () => {
  const [activeBg, setActiveBg] = useState(false);

  useEffect(()=> {
    const handleScroll = () => {
      setActiveBg(window.scrollY > 120);
    };

    window.addEventListener('scroll', handleScroll);

    // Clean up the event listener on component unmount
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  },[]);

  const [navbarOpen, setNavbarOpen] = useState(false);
  const [dropdownOpen, setDropdownOpen] = useState(false);

  const toggleNavbar = () => {
    setNavbarOpen(!navbarOpen);
  };

  const closeNavbar = () => {
    setNavbarOpen(false);
  };

  // Function to toggle dropdown open/close
  const toggleDropdown = () => {
    setDropdownOpen(!dropdownOpen);
  };

  // Function to close dropdown
  const closeDropdown = () => {
    setDropdownOpen(false);
  };

  return (
    <header className={`top_header ${activeBg ? 'active-bg' : ''}`} >
    <nav className="navbar navbar-expand-lg">
      <div className="container">
        <Link className="navbar-brand" to="/">
          <img src={logo} alt="logo" width="200" />
        </Link>
        <button className="navbar-toggler" type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded={navbarOpen ? 'true' : 'false'}
          aria-label="Toggle navigation"
          onClick={toggleNavbar}>
          <span className="navbar-toggler-icon">
            <img src={menuIcon} alt="menu" width="32"/>
          </span>
        </button>

        <div className={`collapse navbar-collapse ${navbarOpen ? 'show' : ''}`} id="navbarSupportedContent">
          <ul className="navbar-nav ms-auto align-items-lg-center">
            <NavItem to="/" text="Home" onClick={() => {
                  closeNavbar();
                  closeDropdown();
                }}/>
            <NavItem to="/about-us" text="About Us" onClick={() => {
                  closeNavbar();
                  closeDropdown();
                }}/>
            <li className="nav-item dropdown">
              <Link
                  className="nav-link dropdown-toggle"
                  to="#"
                  id="navbarDropdown"
                  role="button"
                  data-bs-toggle="dropdown"
                  aria-expanded={dropdownOpen ? 'true' : 'false'}
                  onClick={toggleDropdown}>Organisations</Link>
              <div className={`dropdown-menu custom_dropdown animate slideIn ${dropdownOpen ? 'show' : ''}`} aria-labelledby="navbarDropdown">
                <ul class="sub_nav ps-0">
                
                  <DropdownItem to="/nani-maria-kinder-school" text="Nani Maria Kinder Garten School,
                    Roing ( NMKGS )" onClick={() => {
                      closeNavbar();
                      closeDropdown();
                    }}/>
                  <DropdownItem to="/Specialized-Adoption-Agency" text="Specialized Adoption Agency ( SAA
                  )" onClick={() => {
                      closeNavbar();
                      closeDropdown();
                    }}/>
                  <DropdownItem to="/Children-Care-Institute" text="Children Care Institute ( CCI ) with
                  10 Special Units" onClick={() => {
                      closeNavbar();
                      closeDropdown();
                    }}/>
                    <DropdownItem to="/One-Stop-Centre" text="One Stop Centre ( OSC )" onClick={() => {
                      closeNavbar();
                      closeDropdown();
                    }}/>
                    <DropdownItem to="/after-care" text="After Care" onClick={() => {
                      closeNavbar();
                      closeDropdown();
                    }}/>
                </ul>
              </div>
            </li>
            <NavItem to="/our-photo-gallery" text="Gallery" onClick={() => {
                  closeNavbar();
                  closeDropdown();
                }}/>
            <NavItem to="/contact-us" text="Contact" onClick={() => {
                  closeNavbar();
                  closeDropdown();
                }}/>
            <li className="nav-item">
              <NavLink className="btn nav-link navBtn" to="/donate" onClick={() => {
                  closeNavbar();
                  closeDropdown();
                }}>Donate</NavLink>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  </header>
  );
};


const NavItem = ({ to, text, onClick }) => (
  <li className="nav-item">
    <NavLink
      className="nav-link"
      to={to}
      exact
      activeClassName="active"
      onClick={onClick}
    >
      {text}
    </NavLink>
  </li>
);

const DropdownItem = ({ to, text, onClick }) => (
  <li>
    <NavLink
      className="dropdown-item"
      to={to}
      exact
      activeClassName="active"
      onClick={onClick}
    >
      {text}
    </NavLink>
  </li>
);

export default MyNavbar;
