import React from 'react';
import { RouterProvider, createBrowserRouter, Outlet} from 'react-router-dom';
import MyNavbar from './components/MyNavbar';
import Home from './pages/Home';
import About from './pages/About';
import Contact from './pages/Contact';
import Footer from './components/Footer';
import Gallery from './pages/Gallery';
import NaniMariaKinderSchool from './pages/NaniMariaKinderSchool';
import SpecializedAdoptionAgency from "./pages/SpecializedAdoptionAgency"
import ChildrenCareInstitute from './pages/ChildrenCareInstitute';
import OneStopCare from './pages/OneStopCare';
import AfterCare from './pages/AfterCare';
import ScrollToTop from './components/ScrollToTop';
import Donate from './pages/Donate';

const Layout = () =>{
  return (
    <>
      <ScrollToTop/>
      <MyNavbar/>
      <Outlet />
      <Footer />
    </>
  )
}

const router = createBrowserRouter([
  {
    path: "/",
    element: <Layout />,
    children:[
      {
      path:"/",
      element:<Home/>
    },
    {
      path:"/about-us",
      element:<About/>
    },
    {
      path:"/contact-us",
      element:<Contact/>
    },
    {
      path:"/our-photo-gallery",
      element:<Gallery/>
    },
    {
      path:"/nani-maria-kinder-school",
      element:<NaniMariaKinderSchool/>
    },
    {
      path:"/Specialized-Adoption-Agency",
      element:<SpecializedAdoptionAgency/>
    },
    {
      path:"/Children-Care-Institute",
      element:<ChildrenCareInstitute/>
    },
    {
      path:"/One-Stop-Centre",
      element:<OneStopCare/>
    },
    {
      path:"/after-care",
      element:<AfterCare/>
    },
    {
      path:"/donate",
      element:<Donate/>
    }
    ]
  }
]);


const App = () => {
  return (
    <div>
      <RouterProvider router={router} />
    </div>
  );
};

export default App;
